
  
  .modal {

    
    background-size: 100% 100%;
    background-repeat: no-repeat;
    border-radius: .5cm;
    border: 2px solid rgba(1, 1, 137, 0.41);
 
    overflow-x: scroll;
   
  
  }



.modal {
    background-color: rgba(12, 12, 45, 0.789);
    margin-top: 5vh;
    margin-left: 90vh;
    max-width: 55vw;
    min-width: 55vw;
    max-height: 90vh;
    min-height: 90vh;
    position: absolute; 
    padding: 10px;
    transition: opacity 0.3s ease; /* Transition for fading effect */
}


.modal::-webkit-scrollbar {
    width: 10px;
  }

.close-modal {
    background: none;
	color: inherit;
	border: none;
	font: inherit;
    color: gray;
    
}

.close-modal:hover {
    cursor: pointer;
    font-style: italic;
  
}

p {
  margin: 30px;
}